<template>
    <yd-layout class="appCenter" :class="{'allEdit' : editFlag, 'more' : myApp.length > 4}">
        <div class="homeBox" v-if="searchFlag">
            <div class="search active">
                <input class="searchWord" type="text" v-model="applicationName" placeholder="热门搜索来点我" />
                <span @click="searchFlag = false;applicationName='';searchArr=[];" class="cancel">取消</span>
                <div @click="applicationName='';" class="close">
                    <img src="../../../static/images/delete@3x.png" alt="">
                </div>
            </div>
        </div>
        <div class="searchPage" v-show="searchFlag">
            <dl v-show="searchArr.length > 0">
                <dt>应用</dt>
                <dd v-for="item in searchArr" @click="accessRecord('首页-应用中心-'+item.CHNLNAME);openNew(item);">
                    <yd-flexbox>
                        <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG">
                        <p>
                            <span v-text="list" :class="{ blue: applicationName.includes(list)}" v-for="list in Array.from(item.CHNLNAME)"></span>
                        </p>
                    </yd-flexbox>
                </dd>
            </dl>
            <div class="host" v-if="!isHost&&searchArr.length < 1&&applicationName==''&&searchHostKey.length">
                <span class="tit">历史记录</span>
                <div class="search-button">
                    <a href="javascript:;" @click='applicationName=item' v-for="(item,index) in searchHostKey" :key="index" v-text="item" class="yd-btn yd-btn-primary"></a>
                </div>
            </div>
            <dl v-if="applicationName==''" class="searchRec">
                <dt>搜索推荐</dt>
                <dd v-for="(item,index) in recomApps" v-if="index<4" :key="index" @click="accessRecord('首页-应用中心-'+item.CHNLNAME);openNew(item);">
                    <yd-flexbox>
                        <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG">
                        <p>
                            <span class="tit" v-text='item.CHNLNAME'>星巴克</span>
                            <!-- <br/>
                            <span>
                                咖啡厅 / 茶室 / 应用
                            </span> -->
                        </p>
                    </yd-flexbox>
                </dd>
            </dl>
            <div class="noRes" v-if="searchArr.length < 1&&applicationName!=''">
                <img src="../../../static/images/workCenter/noRestult3.png">
                <p>暂时没有匹配的搜索结果哦</p>
            </div>
        </div>
        <template v-if="!searchFlag">
            <div @touchmove.prevent class="myApp firBox" :class="myApp.length > 4 ? 'more' : ''" v-show="editFlag">
                <div class="colTop nobor">
                    <div class="colLeftTxt">我的应用
                        <span class="litText">(按住拖动排序)</span>
                    </div>
                    <div class="colRightTxt editBox" @click="complete();editFlag=false;">完成</div>
                </div>
                <div class="appbox" :class="myApp.length > 4 ? 'more' : ''" ref="move_app">
                    <div class="move_item" @touchstart="longPress($event)" v-for="(item, index) in myApp" :key="index">
                        <div slot="icon" class="imgBox">
                            <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                            <div @touchstart.stop @click.stop="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                <img src="../../../static/images/app/appDelete.png" alt="">
                            </div>
                        </div>
                        <span class="text" v-text="item.CHNLNAME"></span>
                    </div>
                </div>
            </div>
            <div class="myApp topbor" v-show="!editFlag">
                <div class="colTop ">
                    <div class="colLeftTxt">我的应用
                    </div>
                    <div class="myimg">
                        <img v-for="(item, index) in myApp" :key="index" :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" alt="">
                    </div>
                    <div class="colRightTxt searchBox" @click="searchFlag=true">
                        <img src="../../../static/images/app/appSearch.png" alt="">
                    </div>
                    <div class="colRightTxt editBox" @click="editFlag=true;edit();">编辑</div>
                </div>
            </div>
            <div class="myApp infoRelease" v-if="!editFlag">
                <div class="colTop">
                    <div class="colLeftTxt">重磅更新</div>
                    <div class="colRightTxt" @click="moreNew()">查看更多</div>
                </div>
                <yd-grids-group :rows="4" class="newApps">
                    <yd-grids-item @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in updataApp.slice(0, 4)" :key="index">
                        <div slot="icon" class="imgBox newImgBox">
                            <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                            <div class="new edit" v-if="item.ISRECOMM == 1">
                                <img class="" :class="{'tada' : animateFlag == true}" src="../../../static/images/app/appNew.png" alt="">
                            </div>
                        </div>
                        <span slot="text" v-text="item.CHNLNAME"></span>
                    </yd-grids-item>
                    <yd-grids-item v-for="n in fillNum" :key="n + 'bbbbbbbbb'">
                        <div slot="icon" class="imgBox newImgBox">
                            <img :src="'../../../static/images/app/qidai.png'" />
                        </div>
                        <span slot="text" v-text="'敬请期待'"></span>
                    </yd-grids-item>
                </yd-grids-group>
            </div>
            <div v-if="loadFlag" class="contB">
                <div class="myApp infoRelease">
                    <div class="colTop">
                        <div class="colLeftTxt">信息发布</div>
                        <!-- <div class="colRightTxt moreApp" @click="seeApp(0)">
                        <img src="../../../static/images/app/appFold.png" alt="">
                    </div> -->
                    </div>
                    <yd-grids-group class="appHeight" :class="allOpen[0] ? 'all' : ''" :rows="4">
                        <yd-grids-item @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in allApp['信息发布']" :key="index">
                            <div slot="icon" class="imgBox">
                                <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                                <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                                <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                            </div>
                            <span slot="text" v-text="item.CHNLNAME"></span>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
                <div class="myApp personnel">
                    <div class="colTop">
                        <div class="colLeftTxt">行政服务</div>
                        <!-- <div class="colRightTxt moreApp" @click="seeApp(1)">
                        <img src="../../../static/images/app/appFold.png" alt="">
                    </div> -->
                    </div>
                    <yd-grids-group class="appHeight serviceApps" :class="allOpen[1] ? 'all' : ''" :rows="4">
                        <yd-grids-item @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in allApp['行政服务']" :key="index">
                            <div slot="icon" class="imgBox">
                                <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                                <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                                <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                            </div>
                            <span slot="text" v-text="item.CHNLNAME"></span>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
                <div class="myApp office">
                    <div class="colTop">
                        <div class="colLeftTxt">人事服务</div>
                        <!-- <div class="colRightTxt moreApp" @click="seeApp(2)">
                        <img src="../../../static/images/app/appFold.png" alt="">
                    </div> -->
                    </div>
                    <yd-grids-group class="appHeight personApps" :class="allOpen[2] ? 'all' : ''" :rows="4">
                        <yd-grids-item @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in allApp['人事服务']" :key="index">
                            <div slot="icon" class="imgBox">
                                <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                                <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                                <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                            </div>
                            <span slot="text" v-text="item.CHNLNAME"></span>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
                <div class="myApp report">
                    <div class="colTop">
                        <div class="colLeftTxt">差旅服务</div>
                        <!-- <div class="colRightTxt moreApp" @click="seeApp(3)">
                        <img src="../../../static/images/app/appFold.png" alt="">
                    </div> -->
                    </div>
                    <yd-grids-group class="appHeight sApps" :class="allOpen[3] ? 'all' : ''" :rows="4">
                        <yd-grids-item class="appHeight" @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in allApp['差旅服务']" :key="index">
                            <div slot="icon" class="imgBox">
                                <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                                <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                                <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                            </div>
                            <span slot="text" v-text="item.CHNLNAME"></span>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
                <div class="myApp report">
                    <div class="colTop">
                        <div class="colLeftTxt">生活服务</div>
                    </div>
                    <yd-grids-group class="appHeight" :class="allOpen[3] ? 'all' : ''" :rows="4">
                        <yd-grids-item class="appHeight" @click.native="accessRecord('应用中心-' + item.CHNLNAME);jumpOut(item)" v-for="(item, index) in allApp['生活服务']" :key="index">
                            <div slot="icon" class="imgBox">
                                <img :src="'https://mobileoa.unionpay.com/portalMgmt2/attachments/'+item.IMG" />
                                <div v-if="item.type == 'delete'" @click="deleteApp(item.CHNLNAME)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appDelete.png" alt="">
                                </div>
                                <div v-if="item.type == 'add'" @click="addApp(item)" class="delete" :class="editFlag ? 'edit' : 'common'">
                                    <img src="../../../static/images/app/appAdd.png" alt="">
                                </div>
                            </div>
                            <span slot="text" v-text="item.CHNLNAME"></span>
                        </yd-grids-item>
                    </yd-grids-group>
                </div>
            </div>
        </template>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import $ from '@/jquery-vendor';
export default {
    name: 'appCenter',
    data() {
        return {
            searchArr: [],
            recomApps: [],
            isHost: false,
            searchHost: [],
            searchHostKey: [],
            searchFlag: false,
            applicationName: '',
            editFlag: false,
            allApp: {},
            allOldApp: [],
            updataApp: [],
            myApp: [],
            changeApp: [],
            loadFlag: false,
            allOpen: [false, false, false, false, false],
            aPos: [],
            animateFlag: true,
            Loop: '',
            touchFlag: false,
            animateTime: '',
            fillNum: 0,
            myOldApp: []
        }
    },
    mounted: function () {
        // 8月16号上线
        const vm = this;
        let startY, endY;
        document.setTitle('应用中心');
        this.pageRender();
        this.animateTime = setTimeout(function () {
            vm.animateFlag = false;
        }, 3000);
        // 记录手指触摸的起点坐标
        document.body.addEventListener('touchstart', function (e) {
            startY = e.touches[0].pageY;
        });
        document.body.addEventListener('touchmove', function (e) {
            endY = e.touches[0].pageY; // 记录手指触摸的移动中的坐标
            // 手指下滑，页面到达顶端不能继续下滑
            var scrollTop = document.querySelector('.contB').scrollTop;
            if (endY > startY && scrollTop <= 0 && vm.editFlag) {
                e.preventDefault();
                if (e.cancelable) {
                    // 判断默认行为是否已经被禁用
                    if (!e.defaultPrevented) {
                        e.preventDefault();
                    }
                }
            }
            // // 手指上滑，页面到达底部能继续上滑
            // if (endY < startY && document.documentElement.scrollTop +
            //     $(window).height() >= document.body[0].scrollHeight) {
            //     e.preventDefault();
            // }
        });
        vm.getRecomApps();
    },
    destroyed: function () {
        var vm = this;
        document.body.removeEventListener('touchmove', vm.scrollerFun, false);
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    watch: {
        applicationName(val) {
            this.search(val);
        }
    },
    methods: {
        handleSearchKey(array) {
            let vm = this;
            let n = {};
            let r = [];
            let val;
            let type;
            for (let i = array.length - 1; i >= 0; i--) {
                val = array[i];
                type = typeof val;
                if (!n[val]) {
                    n[val] = [type];
                    r.push(val);
                } else if (n[val].indexOf(type) < 0) {
                    n[val].push(type);
                    r.push(val);
                }
            }
            vm.searchHost = [];
            for (let i = r.length - 1; i >= 0; i--) {
                vm.searchHost.push(r[i]);
            }
            vm.searchHostKey = r;
            return r;
        },
        search(n) {
            const vm = this;
            if (n == '') {
                vm.searchArr = [];
                return;
            }
            vm.get('/portalMgmt/oa/menus/queryApplications/' + n, {}, function (res) {
                vm.searchArr = res.jsonData;
                if (res.jsonData.length > 0) {
                    let searchHost = $.cookie('searchHosts');
                    if (searchHost != undefined && searchHost != '') {
                        vm.searchHost = [];
                        vm.searchHost = searchHost.split(',');
                    }
                    vm.searchHost.push(n);
                    vm.handleSearchKey(vm.searchHost)
                    vm.isHost = vm.searchHostKey.leng > 0;
                    $.cookie('searchHosts', vm.searchHost, { expires: 7, path: '/' });
                }
            });
        },
        openNew(data) {
            const vm = this;
            console.log(data.URL.indexOf('http'))
            if (vm.HOST != 'http://172.18.44.44' && vm.HOST != 'http://localhost:8080') {
                if (data.CHNLNAME == '费控宝') {
                    let dTime = new Date().getTime();
                    let feiUrl = `http://poc.feikongbao.net:8080/h5/#/ssoLogin?loginid=${vm.userInfo.userid}&stamp=${dTime}&token=${vm.SHA1('U2FsdGVkX195zejeVG15PoO7Nlf9q4D34rt0okijn' + vm.userInfo.userid + dTime)}`;
                    UPCHAT.M.NAPI.startNewWebView(feiUrl, { 'sysid': data.SYSID });
                } else {
                    let nUrl;
                    if (data.URL.indexOf('http') != -1) {
                        if (data.URL.indexOf('{username}') != -1) {
                            nUrl = data.URL.replace(/{username}/, vm.userInfo.userid);
                        } else if (data.URL.indexOf('{empcode}') != -1) {
                            nUrl = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                        } else {
                            nUrl = data.URL
                        }
                    } else {
                        nUrl = this.HOST + data.URL;
                    }
                    UPCHAT.M.NAPI.startNewWebView(nUrl, { 'sysid': data.SYSID });
                }
            } else {
                if (data.URL.indexOf('{username}') != -1) {
                    data.URL = data.URL.replace(/{username}/, vm.userInfo.userid);
                    window.location.href = data.URL;
                } else if (data.URL.indexOf('{empcode}') != -1) {
                    data.URL = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                    window.location.href = data.URL;
                } else {
                    window.location.href = data.URL;
                }
            }
        },
        getRecomApps() {
            let vm = this;
            let searchHost = $.cookie('searchHosts');
            if (searchHost != undefined && searchHost != '') {
                vm.searchHost = searchHost.split(',');
            }
            vm.handleSearchKey(vm.searchHost);
            vm.isHost = vm.searchHostKey.leng > 0;
            vm.get('/portalMgmt/oa/menus/menus', {}, function (res) {
                vm.recomApps = res.jsonData;
            });
        },
        jumpOut(data) {
            const vm = this;
            if (!this.editFlag) {
                if (vm.HOST != 'http://172.18.44.44' && vm.HOST != 'http://localhost:8080') {
                    if (data.CHNLNAME == '费控宝') {
                        let dTime = new Date().getTime();
                        let feiUrl = `http://poc.feikongbao.net:8080/h5/#/ssoLogin?loginid=${vm.userInfo.userid}&stamp=${dTime}&token=${vm.SHA1('U2FsdGVkX195zejeVG15PoO7Nlf9q4D34rt0okijn' + vm.userInfo.userid + dTime)}`;
                        UPCHAT.M.NAPI.startNewWebView(feiUrl, { 'sysid': data.SYSID });
                    } else {
                        let nUrl;
                        if (data.URL.indexOf('http') != -1) {
                            if (data.URL.indexOf('{username}') != -1) {
                                nUrl = data.URL.replace(/{username}/, vm.userInfo.userid);
                            } else if (data.URL.indexOf('{empcode}') != -1) {
                                nUrl = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                            } else {
                                nUrl = data.URL
                            }
                        } else {
                            nUrl = this.HOST + data.URL;
                        }
                        UPCHAT.M.NAPI.startNewWebView(nUrl, { 'sysid': data.SYSID });
                    }
                } else {
                    if (data.URL.indexOf('{username}') != -1) {
                        data.URL = data.URL.replace(/{username}/, vm.userInfo.userid);
                        window.location.href = data.URL;
                    } else if (data.URL.indexOf('{empcode}') != -1) {
                        data.URL = data.URL.replace(/{empcode}/, vm.userInfo.empcode);
                        window.location.href = data.URL;
                    } else {
                        window.location.href = data.URL;
                    }
                }
            }
        },
        scrollT(e) {

        },
        pageRender() {
            const vm = this;
            // 我的app
            this.get('/portalMgmt/oa/menus/getPersonMenus', {}, function (res) {
                vm.myApp = res.jsonData;
                vm.myOldApp = res.jsonData.slice(0);
                // 所有app
                vm.get('/portalMgmt/oa/menus/getAllApplications', {}, function (res) {
                    vm.allApp = res.jsonData[0];
                    vm.allOldApp = res.jsonData[0];
                    Object.values(res.jsonData[0]).forEach(function (data) {
                        vm.updataApp.push(...data);
                    });
                    // 判断是添加还是删除的图标
                    vm.chooseType();
                    vm.loadFlag = true;
                    vm.updataApp = vm.updataApp.filter((item) => {
                        return item.ISRECOMM == 1;
                    });
                    if (vm.updataApp.length < 4) {
                        vm.fillNum = 4 - vm.updataApp.length;
                    }
                });
            });
        },
        accessRecord(name) {
            let vm = this;
            if (!this.editFlag) {
                axios.get(vm.conf.portalMobile + '/portal/report/accessRecord', {
                    params: {
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'moduleName': name
                    }
                }).then(function (data4) {});
            }
        },
        edit() {
            const vm = this;
            // setTimeout(() => {
            //     vm.changePos();
            // });
            vm.$nextTick(() => {
                // vm.changePos();
            });
        },
        deleteApp(title) {
            const vm = this;
            document.body.removeEventListener('touchmove', vm.scrollerFun, false);
            if (vm.myApp.length <= 1) {
                this.$dialog.toast({
                    mes: '请至少保留一个应用',
                    timeout: 1500,
                    callback: () => {
                        return false;
                    }
                })
            } else {
                this.backPos();
                vm.myApp = vm.myApp.filter((res) => {
                    return res.CHNLNAME != title;
                });
                this.chooseType();
            }
        },
        addApp(item) {
            document.body.removeEventListener('touchmove', this.scrollerFun, false);
            if (this.myApp.length >= 7) {
                this.$dialog.toast({
                    mes: '最多只能选择7个应用',
                    timeout: 1500,
                    callback: () => {
                        return false;
                    }
                })
            } else {
                const vm = this;
                this.myApp.push(item);
                vm.chooseType();
                // alert(item.type)
            }
        },
        chooseType() {
            const vm = this;
            for (var key in vm.allApp) {
                vm.allApp[key].forEach(function (data) {
                    // let hasNum = vm.myApp.findIndex(function (value, index, arr) {
                    //     return value.CHNLNAME == data.CHNLNAME;
                    // }) // 2
                    vm.myApp.some(function (val) {
                        return val.CHNLNAME == data.CHNLNAME;
                    }) ? data.type = 'delete' : data.type = 'add';
                });
            }
        },
        complete() {
            const vm = this;
            if (vm.myApp.length < 1) {
                vm.myApp = vm.myOldApp;
                return;
            } else {
                // 修改app
                let appId = [];
                vm.myApp.forEach((item) => {
                    appId.push(item.CHNLCODE);
                });
                this.get('/portalMgmt/oa/menus/personMenus/' + appId.join(','), {}, function (res) {});
                let appNum;
                if (window.localStorage.getItem('appNum') == null) {
                    window.localStorage.setItem('appNum', 0);
                } else {
                    appNum = window.localStorage.getItem('appNum');
                    appNum++;
                    window.localStorage.setItem('appNum', appNum);
                }
            }
        },
        seeApp(index) {
            this.$set(this.allOpen, index, !this.allOpen[index]);
        },
        moreNew() {
            const vm = this;
            console.log('aaaaaaaaaaaaaaaaaaaaaaaaaaaaa')
            let nUrl = this.HOST + '/appList';
            if (vm.HOST != 'http://172.18.44.44' && vm.HOST != 'http://localhost:8080') {
                UPCHAT.M.NAPI.startNewWebView(nUrl, { 'sysid': '' });
            } else {
                window.location.href = nUrl;
            }
        },
        changePos() {
            const vm = this;
            let aLi = document.querySelectorAll('.move_item');
            // var zIndex = 2;
            // 布局转换
            console.log('bbbbbbbbbbbbbbbbbb')
            vm.aPos = [];
            for (let i = 0; i < aLi.length; i++) {
                vm.aPos.push({ left: aLi[i].offsetLeft, top: aLi[i].offsetTop });
                aLi[i].style.left = vm.aPos[i].left + 'px';
                aLi[i].style.top = vm.aPos[i].top + 'px';
            }
            for (let j = 0; j < aLi.length; j++) {
                aLi[j].style.position = 'absolute';
                aLi[j].style.margin = 0;
                aLi[j].index = j;
            }
        },
        scrollerFun(event) {
            // 判断默认行为是否可以被禁用
            if (event.cancelable) {
                // 判断默认行为是否已经被禁用
                if (!event.defaultPrevented) {
                    event.preventDefault();
                }
            }
        },
        longPress(ev) {
            const vm = this;
            vm.changePos();
            let oEvt = ev.targetTouches[0];
            let oUl = ev.currentTarget;
            let aLi = document.querySelectorAll('.move_item');
            // let scrollView = document.querySelector('.yd-scrollview');
            let disX = oEvt.clientX - oUl.offsetLeft;
            let disY = oEvt.clientY - oUl.offsetTop;
            vm.touchFlag = true;
            // 按住时开始转换为绝对定位
            clearInterval(this.Loop); // 再次清空定时器
            document.body.addEventListener('touchmove', vm.scrollerFun, false);
            // document.querySelector('.yd-scrollview').addEventListener('touchmove', vm.scrollerFun, false);
            this.Loop = setTimeout(function () {
                // 防止ios头部默认下拉
                document.body.addEventListener('touchmove', vm.scrollerFun, false);
                // 长按开始移动
                // scrollView.hidden = 'hidden';
                // 长按抖动
                // oUl.className = 'shake move_item';
                // setTimeout(function () {
                //     oUl.className = 'move_item';
                // }, 300);
                function fnMove(ev) {
                    let oEvt1 = ev.targetTouches[0];
                    // 交换位置
                    var nearObj = findNearest(oUl); // 碰撞检测+找最近
                    if (nearObj && nearObj != oUl) {
                        var n = oUl.index; // 拿着的obj索引
                        var m = nearObj.index; // 被撞的索引
                        for (var h = 0; h < aLi.length; h++) {
                            // li.index>n && li.index<=mn到m不含n
                            if (aLi[h].index > n && aLi[h].index <= m) {
                                aLi[h].index--;
                                vm.move(aLi[h], vm.aPos[aLi[h].index]);
                            } else if (aLi[h].index < n && aLi[h].index >= m) {
                                // li.index<n && li.index>=m n到m不含n
                                aLi[h].index++;
                                vm.move(aLi[h], vm.aPos[aLi[h].index]);
                            }
                        }
                        oUl.index = m; // obj的索引等于被撞到的near(m);
                    }
                    oUl.style.left = oEvt1.clientX - disX + 'px';
                    oUl.style.top = oEvt1.clientY - disY + 'px';
                }

                function fnEnd() {
                    oUl.removeEventListener('touchmove', fnMove, false);
                    oUl.removeEventListener('touchend', fnEnd, false);
                    document.body.removeEventListener('touchmove', vm.scrollerFun, false);
                    vm.move(oUl, vm.aPos[oUl.index]); // 回自个位置
                    vm.clearLoop();
                }

                function findNearest(obj) {
                    var minDis = 99999999999999; // 距离
                    var minDisIndex = -1; // 下标
                    for (var e = 0; e < aLi.length; e++) {
                        // if(obj==aLi[i]) continue;//放过自己
                        if (collTest(obj, aLi[e])) { // 撞到了
                            // 找最近
                            var dis = getDis(obj, aLi[e]); // 求obj到被撞的房子距离
                            if (dis < minDis) {
                                minDis = dis;
                                minDisIndex = e;
                            }
                        }
                    }
                    if (minDisIndex == -1) { // 没撞到
                        return null;
                    } else {
                        return aLi[minDisIndex]; // 丢出去最近的li
                    }
                }

                function getDis(obj1, obj2) { // obj1到obj2的房子的距离
                    var a = vm.aPos[obj2.index].top - obj1.offsetTop;
                    var b = vm.aPos[obj2.index].left - obj1.offsetLeft;
                    return Math.sqrt(a * a + b * b);
                }

                function collTest(obj1, obj2) { // 要和obj2的房子
                    var l1 = obj1.offsetLeft;
                    var t1 = obj1.offsetTop;
                    var r1 = obj1.offsetLeft + obj1.offsetWidth;
                    var b1 = obj1.offsetTop + obj1.offsetHeight;

                    var l2 = vm.aPos[obj2.index].left // obj2的房子位置
                    var t2 = vm.aPos[obj2.index].top;
                    var r2 = vm.aPos[obj2.index].left + obj2.offsetWidth;
                    var b2 = vm.aPos[obj2.index].top + obj2.offsetHeight;

                    if (l1 > r2 || t1 > b2 || r1 < l2 || b1 < t2) { // obj1和obj2的房子
                        // 没撞到
                        return false;
                    } else {
                        // 撞到
                        return true;
                    }
                }
                oUl.addEventListener('touchmove', fnMove, false);
                oUl.addEventListener('touchend', fnEnd, false);
            }, 300);
        },
        clearLoop() {
            clearInterval(this.Loop);
            this.backPos();
            this.touchFlag = false;
        },
        appStart(ev) {},
        // position回到float
        backPos() {
            const vm = this;
            vm.myOldApp = vm.myApp.slice(0);
            if (vm.touchFlag) {
                let aLi = document.querySelectorAll('.move_item');
                // myAPP的位置同样替换掉
                vm.changeApp = new Array(aLi.length);
                aLi.forEach((item, num) => {
                    vm.changeApp[item.index] = vm.myOldApp[num];
                });
                vm.myApp = vm.changeApp;
                aLi.forEach((item, num) => {
                    item.style.left = 0;
                    item.style.top = 0;
                    item.style.position = 'static';
                });
                vm.touchFlag = false;
            }
        },
        getStyle(obj, attr) {
            return obj.currentStyle ? obj.currentStyle[attr] : getComputedStyle(obj, false)[attr];
        },
        move(obj, json, opational = {}) {
            const vm = this;
            opational.time = opational.time || 300;
            opational.fn = opational.fn || null;
            opational.type = opational.type || 'ease-out';

            var start = {};
            var dis = {};
            for (var key in json) {
                start[key] = parseInt(vm.getStyle(obj, key));
                dis[key] = json[key] - start[key];
            }

            var count = Math.round(opational.time / 30);
            var n = 0;

            clearInterval(obj.timer);
            obj.timer = setInterval(function () {
                n++;
                let a;
                let cur;
                for (var key in json) {
                    // 办事
                    switch (opational.type) {
                        case 'linear':
                            a = n / count;
                            cur = start[key] + dis[key] * a;
                            break;
                        case 'ease-in':
                            a = n / count;
                            cur = start[key] + dis[key] * a * a * a
                            break;
                        case 'ease-out':
                            a = 1 - n / count;
                            cur = start[key] + dis[key] * (1 - a * a * a)
                            break;
                    }
                    if (key == 'opacity') {
                        obj.style.opacity = cur;
                        obj.style.filter = 'alpha(opacity:' + cur * 100 + ')';
                    } else {
                        obj.style[key] = cur + 'px';
                    }
                }
                if (n == count) {
                    clearInterval(obj.timer);
                    opational.fn && opational.fn();
                }
            }, 30);
        }
    }
}
</script>
